*{
  margin: 0px;
  padding: 0px;
  list-style: none;
  text-decoration: none;
  color : #333;
  box-sizing: border-box;
}

.inner{
  width: 90%;
  margin: auto;
  justify-content: center;
}


/* header */
.header{
  border-bottom: 2px solid #eee;
  padding : 2rem 0px;
  background-color: white;
}
.gnb{
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 80px;
}
.gnb li{
  height: 100%;
  padding: 0px 10px;
}
.gnb li a{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
}

.logoImage{
  height: 100%;
}

.gnb li a p{
  font-size: 1.5rem;
  font-weight: bold;
  color : #666;
  transition: 0.2s;
}
.gnb li a p:hover{
  color : #333;
}


/* Main */
.mainWrap{
  margin-top : 3rem;
  display: flex;
  width: 100%;
  height: 400px;
}
.mainLeft{
  width: 60%;
}

.main{
  margin: auto;
  position: relative;
  width: 90%;
  height: 100%;
}
.leftMainWrap{
  width: 100%;
  height: 100%;
  /* padding-bottom: 60%; */
  border-radius: 30px;
  box-shadow: 5px 5px 10px rgba(0,0,0,0.15);

  background-image: url("../public/img/bubble.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

.mainBackGround{
  width: 100%;
  height: 100%;
  position: absolute;
  z-index: -1;
}

.textWrap{
  display: block;
  position: absolute;
  width: 80%;
  bottom: 10%;
  left: 50%;
  transform: translate(-50%);
  height: auto;
}

.mainName{
  font-size: 3rem;
  color : white;
  font-weight: 900;
  margin-bottom : 1rem;
}
.mainSub{
  color : #eee;

}





.mainRight{
  width: 40%;
  padding: 0px 5%;
}
.marinRightTopWrap{
  background-color: white;
  height: 60%;
  border-radius: 1rem;
  position: relative;
}
.selectBtnWrap{
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 100%;
  height: 3rem;
}
.selectBtnWrap div{
  height: 100%;
}
.selectBtnWrap div a{
  height: 100%;

}
.selectBtnWrap div a h2{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  color : #bbb;
}
.selectBtnWrap div a.selected h2{
  color : #333;
  border-bottom : 3px solid rgb(68, 134, 255);
}
.contentsWrap{
  padding-top : 1rem;
  height: 100%;
  position: relative;
}
.notice,.gallery{
  padding : 5% 10%;  
  background-color: white;
}

.notice li{
  margin-top : .5rem;
}
.gallery{
  height: 100%;
  text-align: center;
}
.galleryImage{
  height: 90%;
  margin: auto;
}


.downloadWrap{
  height: 40%;
  margin-top : 2rem;
  border-radius: 1rem;
  background-color: white;
}

.download{
  width: 80%;
  margin: auto;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.download h3{
  font-weight: 900;
  margin-bottom: 10px;
}
.download span{
  color : #555;
}
.download .circle{
  position: absolute;
  width: 30%;
  height: 0%;
  padding-bottom: 30%;
  background-color: lightgreen;
  border-radius: 50%;
  right: 0%;
  top: -30%;
}